                  <b-input-group-text class="bg-gradient-primary">
                    <strong>C</strong>
                  </b-input-group-text>
                </template>
                <b-form-input
                  v-model="triajeData.temperatura"
                  type="number"
                />
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col cols="12">
            <b-form-group
              label="Presin arterial(PA): "
              label-cols-xl="6"
            >
              <b-input-group size="sm">
                <template #append>
                  <b-input-group-text class="bg-gradient-primary">
                    <strong>x mmHg</strong>
                  </b-input-group-text>
                </template>
                <b-form-input type="number" />
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col cols="12">
            <b-form-group
              label="Saturacin (SAT):"
              label-cols-xl="6"
            >
              <b-input-group size="sm">
                <template #append>
                  <b-input-group-text class="bg-gradient-primary">
                    <strong>%</strong>
                  </b-input-group-text>
                </template>
                <b-form-input type="number" />
              </b-input-group>
            </b-form-group>
          </b-col>
          <!-- <b-col cols="12">
            <b-form-group
              label="Frecuencia cardiaca (FC):"
              label-cols-xl="6"
            >
              <b-input-group size="sm">
                <template #append>
                  <b-input-group-text class="bg-gradient-primary">
                    <strong>x min</strong>
                  </b-input-group-text>
                </template>
                <b-form-input type="number" />
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col cols="12">
            <b-form-group
              label="Frecuencia respiratoria (FR):"
              label-cols-xl="6"
            >
              <b-input-group size="sm">
                <template #append>
                  <b-input-group-text class="bg-gradient-primary">
                    <strong>x min</strong>
                  </b-input-group-text>
                </template>
                <b-form-input type="number" />
              </b-input-group>
            </b-form-group>
          </b-col> -->
        </b-row>
        <b-row>
          <b-col cols="12">
            <b-form-group
              label="Peso(*):"
              label-cols-xl="6"
            >
              <b-input-group size="sm">
                <template #append>
                  <b-input-group-text class="bg-gradient-primary">
                    <strong>Kg.</strong>
                  </b-input-group-text>
                </template>
                <b-form-input
                  v-model="triajeData.peso"
                  type="number"
                  @input="calcularICM"
                />
              </b-input-group>
            </b-form-group>
          </b-col>
          <b-col cols="12">
            <b-form-group
              label="Talla(*):"
              label-cols-xl="6"
            >
              <b-input-group size="sm">
                <template #append>
                  <b-input-group-text class="bg-gradient-primary">
                    <strong>cm.</strong>
                  </b-input-group-text>
                </template>
                <b-form-input
                  v-model="triajeData.talla"
                  type="number"
                  @input="calcularICM"
                />
              </b-input-group>
            </b-form-group>
          </b-col>
          <!-- <b-col cols="12">
            <b-form-group
              label="Perimetro abdominal (PAB):"
              label-cols-xl="6"
            >
              <b-input-group size="sm">
                <template #append>
                  <b-input-group-text class="bg-gradient-primary">
                    <strong>cm.</strong>
                  </b-input-group-text>
                </template>
                <b-form-input type="number" />
              </b-input-group>
            </b-form-group>
          </b-col> -->
        </b-row>
      </div>
    </b-card>

    <b-card border-variant="danger">
      <b-row>
        <b-col md="12">
          <div class="d-flex">
            <feather-icon
              icon="ClockIcon"
              size="19"
            />
            <h4 class="mb-0 ml-50">
              Resultado
            </h4>
          </div>
        </b-col>
      </b-row>
      <b-row
        class="justify-contentd-flex justify-content-center"
      >
        <b-col xl="6">
          <!-- readonly input -->
          <b-form-group
            label="Superficie corporal (SC):"
            label-align="right"
            label-cols="9"
          >
            <b-form-input
              value="0"
              readonly
              size="sm"
            />
          </b-form-group>
        </b-col>
        <b-col xl="6">

          <b-form-group
            label="Clasificacin IMC:"
            label-align="right"
            label-cols="8"
          >
            <b-form-input
              value="0"
              readonly
              size="sm"
            />
          </b-form-group>
        </b-col>
      </b-row>

      <b-row
        cols-md="8"
        class="justify-contentd-flex justify-content-center"
      >
        <b-col xl="6">
          <b-form-group
            label="ndice de masa corporal (Valor IMC):"
            label-align="right"
            label-cols="9"
          >
            <b-form-input
              v-model="triajeData.icm"
              readonly
              size="sm"
            />
          </b-form-group>
        </b-col>

        <b-col xl="6">
          <b-form-group
            label="Riesgo de enfermedad:"
            label-align="right"
            label-cols="8"
          >
            <b-form-input
              value="0"
              readonly
              size="sm"
            />
          </b-form-group>
        </b-col>
      </b-row>
      <b-row>
        <b-col cols="12">
          <b-form-group label="Observaciones:">
            <b-form-textarea
              id="textarea"
              v-model="triajeData.observaciones"
              rows="3"
              max-rows="6"
            />
          </b-form-group>
        </b-col>
      </b-row>
    </b-card>

    <b-card>
      <b-row
        cols-md="8"
        class="justify-contentd-flex justify-content-center"
      >
        <b-button
          variant="primary"
          class="mb-1 mb-sm-0 mr-0 mr-sm-1"
          :block="$store.getters['app/currentBreakPoint'] === 'xs'"
          @click="onSubmit"
        >
          Guardar cambios
        </b-button>
      </b-row>

    </b-card>
  </div>
</template>

<script>
import {
  BCard, BRow, BCol, BFormInput, BFormGroup, BFormTextarea, BButton, BInputGroup, BInputGroupText,
} from 'bootstrap-vue'
import store from '@/store'
import {
  toRefs, onUnmounted, ref,
} from '@vue/composition-api'
import ToastificationContent from '@core/components/toastification/ToastificationContent.vue'
import triajeStoreModule from '../triajeStoreModule'

export default {
  components: {
    BCard,
    BRow,
    BCol,
    BFormInput,
    BFormGroup,
    BFormTextarea,
    BButton,
    BInputGroup,
    BInputGroupText,
    // BCardSubTitle,
  },
  props: {
    triajeData: {
      type: Object,
      default() {
        return {}
      },
    },
  },
  data() {
    return {
      edad: null,
    }
  },
  methods: {
    calcularICM() {
      const icm = this.triajeData.peso / (this.triajeData.talla ** 2)
      this.triajeData.icm = Number.parseFloat(icm).toFixed(1)
    },
    onSubmit() {
      this.triajeDataForm = { ...this.triajeData }
      this.triajeDataForm.estado = 'completado'
      if (this.triajeData.id) {
      // Vamos a editar
        console.log('Vamos a editar')
        store.dispatch('triajes/updateTriaje', this.triajeDataForm)
          .then(() => {
            this.$toast({
              component: ToastificationContent,
              props: {
                variant: 'success',
                icon: 'CheckCircleIcon',
                title: 'Actualizacin exitosa',
                hideClose: false,
              },
            })
            this.$router.push({ name: 'salud-triajes-list' })
          }).catch(error => {
            this.$toast({
              component: ToastificationContent,
              props: {
                title: `${error.response.data.error}`,
                icon: 'EditIcon',
                variant: 'danger',
              },
            })
          })
      } else {
        // Vamos a registrar
        console.log('Estamo aptos a registrar')
        store.dispatch('triajes/addTriaje', this.triajeDataForm)
          .then(() => {
            this.$toast({
              component: ToastificationContent,
              props: {
                variant: 'success',
                icon: 'CheckCircleIcon',
                title: 'Registro exitoso',
                text: 'Ya puedes agregar servicios.',
                hideClose: false,
              },
            })
            this.$router.push({ name: 'salud-triajes-list' })
          }).catch(error => {
            this.$toast({
              component: ToastificationContent,
              props: {
                title: `${error.response.data.error}`,
                icon: 'EditIcon',
                variant: 'danger',
              },
            })
          })
      }
    },
  },
  setup(props) {
    const PERSONA_APP_STORE_MODULE_NAME = 'triajes'

    // Register module
    if (!store.hasModule(PERSONA_APP_STORE_MODULE_NAME)) store.registerModule(PERSONA_APP_STORE_MODULE_NAME, triajeStoreModule)

    // UnRegister on leave
    onUnmounted(() => {
      if (store.hasModule(PERSONA_APP_STORE_MODULE_NAME)) store.unregisterModule(PERSONA_APP_STORE_MODULE_NAME)
    })

    const { triajeData } = toRefs(props)

    const triajeDataForm = ref(
      {
        paciente_id: null,
      },
    )
    triajeDataForm.value = triajeData.value

    return {
      triajeDataForm,
    }
  },
}
</script>

<style lang="css" scoped>

  /* Number Type Input Box Scss for - Remove arrow on hover */
  input[type='number']::-webkit-inner-spin-button,
  input[type='number']::-webkit-outer-spin-button {
    -webkit-appearance: auto !important;
  }

</style>
